        fenyeData: [],
        dataIndex: 0, //
        columns: [
                {
                    field: 'custome', width: 100, titleAlign: 'center', columnAlign: 'center',title: '',
                    formatter: function (rowData, index) {
                        return index + 1
                    }, isResize:true,
                },
                {
                    field: 'jobnumber',
                    title: '',
                    width: 150,
                    titleAlign: 'center',
                    columnAlign: 'center',
                    formatter: function(rowData, rowIndex, pagingIndex, field) {
                        return `<span>${rowData[field]}</span>`;
                    },
                    isResize: true,
                    // isFrozen: true,
                },
                {
                    field: 'name',
                    title: '',
                    width: 150,
                    titleAlign: 'center',
                    columnAlign: 'center',
                    isResize: true
                },
                {
                    field: 'department',
                    title: '',
                    width: 200,
                    titleAlign: 'center',
                    columnAlign: 'center',
                    isResize: true
                },
                {
                    field: 'post',
                    title: '',
                    width: 200,
                    titleAlign: 'center',
                    columnAlign: 'center',
                    isResize: true
                },
                {
                    field: 'rank',
                    title: '',
                    width: 150,
                    titleAlign: 'center',
                    columnAlign: 'center',
                    isResize: true
                },
