        this.form.zcName = '',
        this.form.personType = ''
    },
    //
    searchMore() {
        this.form.currentState = this.stateVal
        this.form.sex = this.sex
        this.form.education = this.xueliVal
        this.form.schoolProp = this.schoolVal
        this.form.bzlx = this.bianzhi
