            this.column12 = res.obj
        })
        this.result8 = []
    },
    //
    selectzj(){
        this.showZj = true
    },
    onConfirm11(){
        let str = ''
        let val = ''
        for(let i in this.result9){
            str += this.result9[i].content+','
            val += this.result9[i].code+','
        }
        if(str.length > 0){
            str = str.substr(0,str.length - 1)
            val = val.substr(0,val.length - 1)
        }
        this.zhijiVal = val
        this.form.rank = str
        this.showZj = false
        this.result9 = []
    },
    //
    onLoad(){
        console.log('')
        let queryData = {
            isAll: 'Y',
            pageNum: 2
        }
        queryPerson(queryData).then(res=>{
            this.tableData.push(res.obj)
        })
    },
    //
    changeDept(checked){
        // debugger
        // console.log(this.defaultCheckedKeys)
        if(!checked){
            this.dataList = []
            for(let k in this.defaultCheckedKeys){
                if(this.defaultCheckedKeys[k].depts){
                    this.dataList.push(this.defaultCheckedKeys[k]) 
                    for(let i in this.defaultCheckedKeys[k].depts){
                        this.$refs.tree.setChecked(this.defaultCheckedKeys[k].depts[i],false)
                        this.$refs.tree.setChecked(this.defaultCheckedKeys[k],true)
                    }
                }
            }
        }else{
            for(let m in this.dataList){
                for(let n in  this.dataList[m].depts){
                    this.$refs.tree.setChecked(this.dataList[m].depts[n],true)
                }
            }
        }
        // this.$refs.tree.setChecked(this.deptData, false)
        // console.log(checked)
    },
    //
    unique(arr){
        var newArr= [];
        var arrId = [];
        for(var item of arr){
            if(arrId.indexOf(item['title']) == -1){
                arrId.push(item['title']);
                newArr.push(item);
            }
        }
        return newArr
    },
    ...mapMutations({
        save_jobNum:'save_jobNum',
    }),
    //
    // setTime(){
    //     this.intertimer = setInterval(() => {
            
    //     }, 3000);
    // }
  },
  mounted() {
     this.$dragging.$on('dragged', ({ value }) => {
            // console.log(value.item)
            console.log(value.list)
            this.newList = value.list
            // console.log(value.otherData)
        })
        this.$dragging.$on('dragend', () => {
    
        })
        console.log(this.$refs.table)
},
  components: {
      draggable
  },
}
</script>
<style>
    td{
        vertical-align: bottom
    }
</style>
<style lang="stylus" scoped>
    .header{
        padding 10px
        box-shadow 0 0  10px #eee
        .btn{
            padding 5px
            line-height 35px
        }
    }
    .table{
        width 100%
        overflow-x auto
    }
    .more{
        font-size 14px
        padding 10px
    }
    .pick{
        padding 10px
        line-height 25px
    }
    .van-checkbox{
        padding 5px
    }
    .eletable{
        overflow-y auto
    }
    .infoitems{
      padding 10px 10px 10px 0 
      flex-direction: row
      flex-wrap wrap
      .infoitem{
        cursor: move
        line-height 35px
        margin 5px
        border-radius 6px
        padding 5px
        font-weight 700
        border 1px solid #eee
        img{
          display block
          width 50px
          height 50px
          margin 0 auto
        }
        span{
          font-size 14px
        }
      }
    }
    .van-tag--warning{
        line-height 19px
    }
</style>


